export default class LocalStorage {
    save(key, value) {
        localStorage.setItem(key, JSON.stringify(value));
        return true;
    }
    fetch(key) {
        const data = localStorage.getItem(key);
        if (data === null)
            return false;
        let daTa;
        try {
            daTa = JSON.parse(data);
        }
        catch (e) {
            console.error(e);
        }
        return daTa;
    }
    clearLocalStorage() {
        localStorage.clear();
    }
}
