import jwtDecode from 'jwt-decode';
import { showMessage } from '../../utils/showMessage';
const SERVER = process.env.SERVER;
export function getAuthUserData() {
    const token = localStorage.getItem('token');
    if (!token)
        return false;
    return jwtDecode(token);
}
export async function getUsers() {
    try {
        const response = await fetch(`${SERVER}/user`);
        const result = await response.json();
        return result;
    }
    catch (err) {
        return [];
    }
}
export async function changeUser(user) {
    try {
        const token = localStorage.getItem('token');
        const response = await fetch(`${SERVER}/user`, {
            method: 'PUT',
            body: user,
            headers: {
                'authorization': `Bearer ${token}`,
            },
        });
        const result = await response.json();
        const newToken = result.accessToken;
        if (response.status === 201) {
            localStorage.setItem('token', newToken);
            showMessage('Пользователь успешно изменён');
            setTimeout(() => window.location.href = process.env.USER_PAGE, 2500);
        }
        else
            showMessage(`Ошибка ${response.status}`, true);
    }
    catch (err) {
        if (err instanceof Error)
            showMessage('Проблемы с подключением к серверу', true);
    }
}
export const allUsers = await getUsers();
