//import { getApplies } from "../../model/api/applies";
import { getAuthUserData } from '../../model/api/users';
import { allAppliesWithUsers } from './createAppliesWithUser';
export const getOpenApplies = (applies) => applies.filter(apply => apply.open);
export const getClosedApplies = (applies) => applies.filter(apply => !apply.open);
export const getMyCreatedApplies = (applies) => {
    const user = getAuthUserData();
    if (!user)
        return [];
    return applies.filter(apply => apply.userId === user.id);
};
export const getNotMyApplies = (applies) => {
    const user = getAuthUserData();
    return applies.filter(apply => apply.userId !== user.id);
};
export const getMyParticipateApplies = (applies) => {
    const user = getAuthUserData();
    if (!user)
        return [];
    return applies.filter(apply => apply.participants.some(p => p === user.id));
};
export const getNotMyParticipateApplies = (applies) => {
    const user = getAuthUserData();
    if (!user)
        return [];
    return applies.filter(apply => !apply.participants.some(p => p === user.id));
};
export const allApplies = await allAppliesWithUsers();
