export const getUsersRating = (users) => users.sort((a, b) => b.goodThings - a.goodThings);
export const getParticipantsInApply = (users, apply) => users.filter(user => apply.participants.some(part => part === user.id));
export const getParticipantsInThanks = (users, thanks) => users.filter(user => thanks.participants.some(thank => thank === user.id));
